  //走帮服
  helpsList: [], //走帮服列表
  helpsListTotal: 0,
  helpsBranch: [], //支部列表
  helpsBranchSelect: [],
  helpsPerson: [], //党员列表
  helpsPersonSelect: [],
  personHelps: [], //党员走帮服列表
  helpDetail: {},

  //党费缴纳
  feesBranch: [], //支部缴费
  feesPerson: [], //党员缴费
  feesBranchSelect: [],
  feesPersonSelect: [],

  //党员积分
  integralQuarterList: [], //积分列表
  integraYearlList: [], //积分列表
  integralQuarterSelect: [],
  integraYearSelect: [],
  integralQuarterTotal: 0,
  integraYearlTotal: 0,
  //消息中心
  newsLists: [],
  unNewsNums: 0,

  //出错信息
  errorAnNewsMsg: '',

  homeNewsScrollTop: 0,
  lessonScrollTop: 0,
  lessonBranchScrollTop: 0,
