
import Vue from "vue";
import BScroll from "better-scroll";
import Loading from 'vux/src/components/loading/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { mapState, mapGetters } from "vuex";
import rem from "./config/rem.js";
export default {
directives: {
TransferDom
},
components: {
Loading,
ViewBox,
Tabbar,
TabbarItem
},
created() {
let date = new Date();
date = date.getTime();
if (
window.localStorage.getItem("time") &&
date - window.localStorage.getItem("time") > 86400000
) {

this.$router.replace("/login");
}
},
name: "app",
computed: {
...mapState({
isLoading: state => state.common.isLoading,
direction: state => state.common.direction
}),
isTabbarDemo() {
if (!this.direction) return "";
return this.$route.path.split("/").length > 2;
}
},
data() {
return {
draftShow: true,
draftFiles: {},
fileArrs: [],
draftNum: 1,
draftMsg: [],
enterAnimate: "", //页面进入动效
leaveAnimate: "" 
};
},
methods: {










































































},
watch: {
$route(to, from) {
const toDepth = to.path.split("/").length;
const fromDepth = from.path.split("/").length;

this.enterAnimate =
toDepth > fromDepth ? "animated fadeInRight" : "animated fadeInLeft";
this.leaveAnimate =
toDepth > fromDepth ? "animated fadeOutLeft" : "animated fadeOutRight";

if (toDepth == 2) {
this.enterAnimate = "";
this.leaveAnimate = "";
}
}
},





};



