import axios from 'axios'
import qs from 'qs'
import _this from '../main.js';


// axios 配置
// axios.defaults.timeout = 8000;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
axios.defaults.headers.post['x-requested-with'] = 'xmlhttprequest'; //ajax请求
axios.defaults.headers.get['x-requested-with'] = 'xmlhttprequest'; //ajax请求
axios.defaults.headers.common['ZT-Mobile'] = 'weixin';

axios.defaults.baseURL = 'http://rugao.ztemap.com:8086/api';

// post传参序列化
axios.interceptors.request.use((config) => {
  if (config.method === 'post') {
    config.data = qs.stringify(config.data)
  }
  return config;
}, (error) => {
  _this.$vux.toast.text('请求超时', 'top', '800')
  return Promise.resolve(error);
});

// 返回状态判断
axios.interceptors.response.use(response => {
  return response
}, error => {
  if (error && error.response) {
    switch (error.response.status) {
      case 400:
        error.message = '请求错误'
        break

      case 401:
        error.message = '未授权，请登录'
        break

      case 403:
        error.message = '拒绝访问'
        break

      case 404:
        error.message = `请求地址出错: ${err.response.config.url}`
        break

      case 408:
        error.message = '请求超时'
        break

      case 500:
        error.message = '服务器内部错误'
        break

      case 501:
        error.message = '服务未实现'
        break

      case 502:
        error.message = '网关错误'
        break

      case 503:
        error.message = '服务不可用'
        break

      case 504:
        error.message = '网关超时'
        break

      case 505:
        error.message = 'HTTP版本不受支持'
        break

      default:
    }
  }
  _this.$vux.toast.text(error.message, 'top', '800')
  return Promise.resolve(error);
})

export function fetch(url, params, method, config) {
  return new Promise((resolve, reject) => {
    if (method == 'get') {
      axios({
          url: url,
          params: params,
          method: method
        }).then(response => {
          resolve(response.data);
        }, err => {
          reject(err);
        })
        .catch((error) => {
          reject(error);
        })
    } else if (method == 'post') {
      axios.post(url, params, config).then(response => {
          resolve(response.data);
        }, err => {
          reject(err);
        })
        .catch((error) => {
          reject(error)
        })
    }
  })
}

