

import Vue from 'vue'
import FastClick from 'fastclick'
import App from './App'
import router from './router'
import store from './store/'
import './config/rem'
import filters from './config/index'
import AlertPlugin from 'vux/src/plugins/alert/index.js'

import ToastPlugin from 'vux/src/plugins/toast/index.js'

import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'


Vue.use(AlertPlugin)
Vue.use(ConfirmPlugin)
Vue.use(ToastPlugin)

Vue.prototype.$msg = function (msg) {
this.$vux.toast.text(msg, 'top','500')
}


FastClick.attach(document.body)

filters(Vue)

const history = window.sessionStorage

history.clear()
let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)

router.beforeEach(function (to, from, next) {
store.dispatch('updateLoadingStatus', {
isLoading: true
})


const toIndex = history.getItem(to.path)
const fromIndex = history.getItem(from.path)

if (toIndex) {
if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
store.dispatch('updateDirection', {
direction: 'forward'
})
} else {
store.dispatch('updateDirection', {
direction: 'reverse'
})
}
} else {
++historyCount
history.setItem('count', historyCount)
to.path !== '/' && history.setItem(to.path, historyCount)
store.dispatch('updateDirection', {
direction: 'forward'
})
}

next()

if (to.matched.some(record => record.meta.requiresAuth)) {
const user = window.localStorage.getItem('user');
if (user) {
next()
} else {
next({
path: '/login'
})
}
} else {
next()
}

})

router.afterEach(function (to) {
store.dispatch('updateLoadingStatus', {
isLoading: false
})
})

Vue.config.productionTip = false

/* eslint-disable no-new */
let that = new Vue({
router,
store,
render: h => h(App)
}).$mount('#app')

export default that


